@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Nunito+Sans:wght@300;400;800&display=swap");

.web__navbar {
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.web__navbar-logo a {
  text-decoration: none;
  color: white;
  font-family: var(--body-font);
}
.web__navbar-links {
  display: flex;
  gap: 50px;
  word-spacing: 2px;
  font-family: var(--body-font);
}
.web__navbar-links a {
  text-decoration: none;
  color: white;
  transition: 0.3s;
}
.web__navbar-links a:hover {
  border-bottom: 2px solid gray;
}

.web_header {
  padding: 2rem 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
}
.header-social button {
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
  text-align: center;
}
.wa {
  background: #128c7e;
}
.fb {
  background: #4267b2;
}

.home__carousel {
  padding: 6rem 4rem;
}
.carousel_img {
  height: 500px;
}

.home__drop {
  padding: 0 4rem;
}
.home__drop button {
  width: 100%;
  height: auto;
  background: rgba(245, 245, 245, 0.883);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 16px;
  font-weight: 500;
}
.drop__card {
  padding: 1rem 2rem;
  border: 2px solid white;
  border-radius: 5px;
}
.drop__card-content {
  padding: 1rem 1rem;
  gap: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.content {
  width: 120px;
  height: 60px;
  background: white;
  color: black;
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: none;
  border-radius: 5px;
}

.home__bar {
  padding: 2rem 4rem;
}
.home__bar ul {
  border: 2px solid white;
  border-radius: 5px;
  height: 120px;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.home__bar ul > li {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  color: white;
}
.home__bar li > span {
  font-size: 45px;
  cursor: pointer;
}
.home__bar li > a {
  text-decoration: none;
  color: white;
}

.home__artikel {
  padding: 2rem 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.title {
  background: white;
  color: black;
  width: 100%;
  border: none;
  border-radius: 5px;
  font-size: 26px;
}
.artikel__content {
  text-align: start;
  padding: 2rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.content__card {
  border: 1px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 15px;
  width: 400px;
  height: 500px;
}
.content__card img {
  width: 300px;
}
.card-title,
.card-desc {
  border-radius: 5px;
  width: 300px;
  padding: 15px 10px;
}
.card-btn button {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 500;
  transition: 0.3s;
}
.card-btn button:hover {
  background: rgba(255, 255, 255, 0.685);
}
.footer {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  color: black;
}
.footer p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.desa__pem {
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  color: white;
  padding: 6rem 4rem;
}

.desa__pem-feature {
  width: 500px;
  padding-bottom: 2rem;
}

.desa__pem-feature {
  margin: 0;
}
.desa__pem-feature {
  max-width: 700px;
}

.bloc-tabs {
  display: flex;
}
.tabs {
  padding: 15px;
  text-align: center;
  width: 50%;
  background: transparent;
  color: white;
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.274);
  box-sizing: content-box;
  position: relative;
  outline: none;
}
.tabs:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs {
  border-bottom: 1px solid transparent;
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
}

button {
  border: none;
}
.content-tabs {
  flex-grow: 1;
}
.content {
  background: transparent;
  padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
  line-height: 20px;
  text-align: start;
}
h1 {
  padding: 0px 0 15px 0px;
  line-height: 30px;
}
hr {
  width: 100px;
  height: 1px;
  border: none;
  margin-bottom: 5px;
}
h4 {
  text-align: left;
  color: white;
}
p {
  color: white;
  line-height: 25px;
}
.active-content {
  display: block;
}
li {
  position: relative;
  left: -15px;
  list-style-type: decimal;
}
